<template>
  <div class="login">
    <div class="con">
      <h3>登录</h3>
      <el-input v-model="user.username" placeholder="请输入用户名"></el-input>
      <el-input v-model="user.password" type="password" placeholder="请输入密码"></el-input>
     <div> <el-button type="primary" @click="smit">登录</el-button></div>
    </div>
  </div>
</template>

<script>
import { successAlert } from '../../utils/alert';
import {requestLogin} from '../../utils/request';
import {mapActions} from 'vuex'
export default {
  data(){
    return {
      user:{
        username:'',
        password:''
      }
    }
  },
    methods:{
      ...mapActions({
        'requestUser':'userActions'
      }),
        smit(){
          requestLogin(this.user).then(res=>{
            if(res.data.code==200){
              successAlert(res.data.msg)
              this.requestUser(res.data.list)
            this.$router.push('/')
            }
          })
        }
    }
};
</script>

<style scoped>
.login {
  background: linear-gradient(#333, #777);
  width: 100vw;
  height: 100vh;
}
.con {
  width: 450px;
  height: 300px;
  border-radius: 10px;
  position: fixed;
  left: 50%;
  top: 50%;
  background: white;
  transform: translate(-50%, -50%);
  text-align: center;
}
h3 {
  line-height: 30px;
  margin-top: 20px;
  font-size: 23px;
}
.el-input {
  width: 90%;
  margin-top: 20px;
}
.el-button {
  margin-top: 20px;
  width: 90%;
}
</style>


